#mapid {
  height: 568px;
}

/* The Modal (background) */
.modal {
  display: block; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

/* Modal Content/Box */
.modal-content {
  z-index: 100001; /* Sit on top */
  background-color: #fefefe;
  margin: 15% auto; /* 15% from the top and centered */
  padding: 20px;
  border: 1px solid #888;
  width: 80%; /* Could be more or less, depending on screen size */
}

/* The Close Button */
.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

div.absolute {
  z-index: 100001; /* Sit on top */
  position: absolute;
  top: 10px;
  left: 10px;
  width: 200px;
  height: 120px;
  color: rgba(255,255,255); /* White w/ opacity */
  background-color: rgba(0,0,0,0.5); /* Red w/ opacity */
}

#myInput {
  /*background-image: url('/css/searchicon.png');*/ /* Add a search icon to input */
  /*background-position: 10px 12px;*/ /* Position the search icon */
  /*background-repeat: no-repeat;*/ /* Do not repeat the icon image */
  min-width: 100%;
  font-size: 12px;
  padding: 3px 0 3px 0;
  /*margin: 5px 5px 5px 5px;*/
  border: 0;
  background-color: rgba(255,255,255,0.4); /* Red w/ opacity */
  /*border: 1px solid #ddd;*/ /* Add a grey border */
  /*margin-bottom: 12px;*/ /* Add some space below the input */
}

#myUL {
  /* Remove default list styling */
  list-style-type: none;
  padding: 0;
  margin: 0;
}

#myUL li {
  /*border: 1px solid #ddd;*/
  /*margin-top: -1px;*/
  /*background-color: #f6f6f6;*/
  /*padding: 2px;*/ /* Add some padding */
  padding: 3px 0 3px 0;
  text-decoration: none; /* Remove default text underline */
  font-size: 12px; /* Increase the font-size */
  /*color: black;*/ /* Add a black text color */
  display: block; /* Make it into a block element to fill the whole list */
}

#myUL li:hover:not(.header) {
  background-color: #eee; /* Add a hover effect to all links, except for headers */
}



